export class ApartmentHouses {
    constructor () {
        this.ApartmentAPIAddress = 'https://localhost:55683';
    }

    // 1.   
    async GetHouses() {
        try {
            const response = await fetch(`${API_BASE}/api/House`, {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });
            const data = await response.json();
            if (!response.ok) throw new Error(data);
            console.log(':', data);
            return data;
        } catch (error) {
            console.error('   :', error);
        }
    }

    // 2.    ID
    async GetHouse(id) {
        try {
            const response = await fetch(`${API_BASE}/api/House/${id}`, {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });
            const data = await response.json();
            if (!response.ok) throw new Error(data);
            console.log(` ${id}:`, data);
            return data;
        } catch (error) {
            console.error(`   ${id}:`, error);
        }
    }

    // 3.   
    async CreateHouse(houseData) {
        try {
            const response = await fetch(`${API_BASE}/api/House`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(houseData)
            });
            const data = await response.json();
            if (!response.ok) throw new Error(data);
            console.log(' :', data);
            return data;
        } catch (error) {
            console.error('  :', error);
        }
    }

    // 4.  
    async UpdateHouse(id, houseData) {
    try {
        const response = await fetch(`${API_BASE}/api/House/${id}`, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(houseData)
        });
        if (!response.ok) {
        const error = await response.text();
        throw new Error(error);
        }
        console.log(` ${id} `);
    } catch (error) {
        console.error(`   ${id}:`, error);
    }
    }

    // 5.  
    async DeleteHouse(id) {
        try {
            const response = await fetch(`${API_BASE}/api/House/${id}`, {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' }
            });
            if (!response.ok) throw new Error(await response.text());
            console.log(` ${id} `);
        } catch (error) {
            console.error(`   ${id}:`, error);
        }
    }

    // 6.    
    async AssignHead(houseId, userId) {
        try {
            const response = await fetch(`${API_BASE}/api/House/${houseId}/head/${userId}`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' }
            });
            const data = await response.text();
            if (!response.ok) throw new Error(data);
            console.log(data);
        } catch (error) {
            console.error(`     ${houseId}:`, error);
        }
    }

    // 7.    
    async RevokeHead(houseId) {
        try {
                const response = await fetch(`${API_BASE}/api/House/${houseId}/head`, {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' }
            });
            const data = await response.text();
            if (!response.ok) throw new Error(data);
            console.log(data);
        } catch (error) {
            console.error(`     ${houseId}:`, error);
        }
    }

    // 8.      
    async GetHead(houseId) {
        try {
                const response = await fetch(`${API_BASE}/api/House/${houseId}/head`, {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });
            const data = await response.json();
            if (!response.ok) throw new Error(data);
            console.log(`   ${houseId}:`, data);
            return data;
        } catch (error) {
            console.error(`     ${houseId}:`, error);
        }
    }

    // 9.    ID 
    async GetHousesByUserId(userId) {
        try {
                const response = await fetch(`${API_BASE}/api/House/user/${userId}`, {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });
            const data = await response.json();
            if (!response.ok) throw new Error(data);
            console.log(`  ${userId}:`, data);
            return data;
        } catch (error) {
            console.error(`     ${userId}:`, error);
        }
    }
}